import React from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { handleMenu } from "../redux/actions/globalActions";

const SideNavItem = ({ name, link, icon, handleMenu }) => {
  return (
    <li className='sidenav__item' onClick={handleMenu}>
      {name === "Contact" ? (
        <Link to='/contact'>
          <span>{name}</span>
          {icon ? icon : ""}
        </Link>
      ) : (
        <a href={link} rel='noreferrer' target='_blank'>
          <span>{name}</span>
          {icon ? icon : ""}
        </a>
      )}
    </li>
  );
};

export default connect(null, { handleMenu })(SideNavItem);
